.mainContainer {
  margin: 10% 15%;
}

.trendingPost {
  margin-top: 50px;
}

.gridContainer {
  margin-top: 15px;
  display: grid;
  grid-template-columns: 65% 35%;
  gap: 5px;
  box-sizing: border-box;
}

.gridItem {
  border: 1px solid black;
}

img {
  width: 100%;
  height: 100%;
}

#gridItem1 {
  grid-row-start: 1;
  grid-row-end: 3;
  height: 360px;
}
#gridItem2 {
  height: 175px;
}
#gridItem3 {
  height: 175px;
}

.latestPost {
  margin-top: 50px;
}

.filtersContainer {
  margin: 15px 0px;
}

.filterbtn {
  margin: 5px;
  border-radius: 25px;
  padding: 5px 10px;
  /* border-style: none; */
  color: #0084ff;
  border-color: #0084ff;
  border-width: 1px;
}
