.navigation {
  position: fixed;
  background-color: white;
  width: 100%;
  height: 80px;
  padding-top: 10px;
  box-shadow: 0 1px 1px #ccc;
  top: 0;
}

.navbar {
  margin-left: 7%;
}

header {
  display: flex;
  justify-content: space-between;
}

.Logo_xd {
  font-size: 2.1em;
  margin-top: 0;
  width: 100%;
  font-family: Raleway, sans-serif;
  font-weight: 900;
  letter-spacing: -0.9px;
  text-align: left;
}

.Logo_lol {
  background-image: linear-gradient(269deg, #0048ff, #0096ff), linear-gradient(#000, #000);
  color: #0084ff;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.logotext {
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 1em;
  font-weight: 500;
  font-style: italic;
  font-stretch: normal;
  line-height: 0.8;
  letter-spacing: 0.9px;
  color: #000;
}

.dropdown {
  margin-top: 20px;
}

.drpdwn {
  color: rgba(0, 0, 0, 0.6);
  font-weight: 600;
}

img {
  width: 20px;
  height: 20px;
}

.navigation p {
  color: rgba(0, 0, 0, 0.4);
  font-size: 15px;
}

.main_button {
  width: auto;
  height: 40px;
  min-width: 100px;
  word-wrap: break-word;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  padding: 8px 32px;
  margin-right: 70px;
  margin-top: 20px;
  color: #fff;
  outline: none;
  font-family: Raleway, sans-serif;
  font-size: 0.9em;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 0.03em;
  background: #0084ff;
  background: linear-gradient(269deg, #0048ff, #0096ff);
}
