.footer {
  background-color: #172146;
  margin: 20px 0 0 0;
  padding: 30px 120px;
}

ul {
  list-style: none;
  padding: 0%;
}

.footer-col li a {
  color: white;
  line-height: 25px;
  font-size: small;
  text-decoration: none;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.footer-col {
  width: auto;
  padding: 0 15px;
}

.footer-col h4 {
  text-transform: uppercase;
  color: hsla(0, 0%, 100%, 0.5);
  font-weight: 700;
}

i {
  display: inline-block;
  margin: 0 10px 10px 0;
}
